export default {
	data() {
		return {
			CustomBar: this.CustomBar,
			TabCur: 0,
			tabNav: ['Flex布局', 'Grid布局', '辅助布局'],
			bgImgStr: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big1000'
		}
	},
	methods: {
		tabSelect(e) {
			let that = this
			let id = e.currentTarget.id
			if (id == that.TabCur) {
				return
			}
			that.TabCur = id
		}
	}
}
